import { TIngredient } from "../types/data";
import { TIngredientsActions } from "../action-creators";
import { 
    GET_INGREDIENTS, 
    GET_INGREDIENTS_FAILED, 
    GET_INGREDIENTS_SUCCESS 
} from "../action-types";

export type TIngredientsState = {
    dataRequest: boolean;
    dataFailed: boolean;
    ingredients: TIngredient[]
}

export const initialIngredientsState: TIngredientsState = {
    dataRequest: false,
    dataFailed: false,
    ingredients: []
}
export const ingredients = (state = initialIngredientsState, action: TIngredientsActions) => {
    switch (action.type) {
        case GET_INGREDIENTS: {
            return {
                ...state,
                //   
                dataRequest: true,
                //        
                //  ,       
                dataFailed: false,
            };
        }
        case GET_INGREDIENTS_SUCCESS: {
            return {
                ...state,
                //   ,     
                ingredients: action.data,
                //    
                dataRequest: false
            };
        }
        case GET_INGREDIENTS_FAILED: {
            return {
                ...state,
                //    , 
                //     
                dataFailed: true,
                //    
                dataRequest: false,
                ingredients: []
            };
        }
        default: {
            return state
        }
    }
}