import { TOrderActions } from "../action-creators";
import { 
    GET_ORDER, 
    GET_ORDER_FAILED, 
    GET_ORDER_SUCCESS, 
    RESET_ORDER 
} from "../action-types";

export type TOrderState = {
    dataRequest: boolean;
    dataFailed: boolean;
    order: {
        name: string;
        number: number;
    }
}

export const initialOrderState: TOrderState = {
    dataRequest: false,
    dataFailed: false,
    order: {
        name: '',
        number: 0
    }
}
export const order = (state = initialOrderState, action: TOrderActions) => {
    switch (action.type) {
        case GET_ORDER: {
            return {
                ...state,
                //   
                dataRequest: true,
                //        
                //  ,       
                dataFailed: false,
            };
        }
        case GET_ORDER_SUCCESS: {
            return {
                ...state,
                //   ,     
                //order: action.data, 
                order: {
                    name: action.data?.name,
                    number: action.data?.order.number
                },
                //    
                dataRequest: false
            };
        }
        case GET_ORDER_FAILED: {
            return {
                ...state,
                //    , 
                //     
                dataFailed: true,
                //    
                dataRequest: false,
                order: {
                    name: '',
                    number: 0
                }
            };
        }
        case RESET_ORDER: {
            return initialOrderState;
        }
        default: {
            return state
        }
    }
}